/**
* border radius
*/

.btn,.jumbotron,.nav:hover{
    border-radius: 0px;
}

.card-body .image{
    max-height: 100px;
    min-height: 100px;
}

/** 
*/
.cart-badge{
    border-radius: 30%;
    padding: 0.2rem;
    font-size: 8px;
    background: #e73c7e;
    color: white;
}

.cart-badge hover{
    color: orange;
}
/**
* singe product page - product name
*/

